import React, { Component } from 'react';

import withAuthorization from '../components/Session/withAuthorization';
import { firebase, db } from '../firebase';
import Layout from '../components/layout';
import { Link } from 'gatsby';
import * as routes from '../constants/routes';

class HomePage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      lists: [],
      listName: '',
      errorMessage: '',
    };
  }

  //firebase.auth.onAuthStateChanged move to layout
  componentDidMount() {
    if (typeof window !== 'undefined') {
      firebase.auth.onAuthStateChanged(authUser => {
        if (authUser) {
          db.readLists().then(snapshot => {
            this.setState(() => ({ lists: snapshot }))
          });
        }
      });
    }
  }

  render() {
    const onListNameChange = (e) => {
        this.setState({
          listName: e.target.value,
          errorMessage: '',
        });
    }

    const onCreateList = () => {
      const listName = this.state.listName;

      if(listName !== '') {
        db.createNewList(listName).then(uid => {
          window.location.href = `list?id=${uid}`;
        })
      } else {
        this.setState({errorMessage: 'list name cannot be empty'})
      }
    }

    const { lists, listName, errorMessage } = this.state;

    return (
      <Layout>
        <HomePageContent
          lists={lists}
          listName={listName}
          onListNameChange={onListNameChange}
          onCreateList={onCreateList}
          errorMessage={errorMessage}
        />
      </Layout>
    );
  }
}

const authCondition = authUser => !!authUser;

const HomePageContent = withAuthorization(authCondition)(({lists, listName, onListNameChange, onCreateList, errorMessage}) => (
    <div>
      {lists.length !== 0 &&
        <div>
          <h2>Finished lists</h2>
          <ul>
          {lists.map((element, index)=> (
            element.isDraw && <li key={index}>
            <Link to={routes.LIST + `?id=${element.uid}`}>{element.name} </Link>
            </li>
          ))}
          </ul>
          <h2>Unfinished lists</h2>
          <ul>
          {lists.map((element, index)=> (
            !element.isDraw && <li key={index}>
            <Link to={routes.LIST + `?id=${element.uid}`}>{element.name} </Link>
            </li>
          ))}
          </ul>
        </div>}
      {lists.length === 0  && <div>Nothing here</div>}
      <div>
        <input placeholder="list name..." defaultValue={listName} onChange={onListNameChange}/>
        {errorMessage && <div>{errorMessage}</div>}
        <button onClick={onCreateList} disabled={errorMessage || listName===''}>Create new list</button>
      </div>
    </div>
));

export default HomePage;
